@use "../../App.scss" as App;
@import "../../App.scss";

.wrapper {
  @include wrapper;
  background-image: url("../../../public/images/main1.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 100vh;
}
.main {
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  justify-content: center;
}
.header {
  font-size: 72px;
  font-family: App.$openSansFont;
  font-weight: 550;
  line-height: 1.17;
  letter-spacing: 1px;
}
.smallHeader {
  margin-top: 40px;
  font-size: 22px;
  line-height: 1.55;
  font-weight: 300;
  width: 50%;
  text-align: center;
  margin-bottom: 70px;
}
.arrowDown {
  position: absolute;
  bottom: 0;
  width: 40px;
  margin-bottom: 50px;
  animation: slideDown 2s ease-in-out infinite;
  cursor: pointer;
}

@keyframes slideDown {
  0% {
    margin-bottom: 50px;
  }
  50% {
    margin-bottom: 40px;
  }
}
